// fileName: App.js (Updated with Admin Routes)

import { BrowserRouter, Routes, Route, useNavigate, useLocation } from 'react-router-dom';
import { useState, useEffect } from 'react';
import HomePage from './components/home/HomePage';
import AcademicInstitution from './components/institution/AcademicInstitution';
import MyVerifiED from './components/student/MyVerifiED';
import AdminDashboard from './components/admin/AdminDashboard';
import Login from './components/common/Login';
import Navigation from './components/common/Navigation';

function AppContent() {
  const [user, setUser] = useState(null);
  const navigate = useNavigate();
  const location = useLocation();
  
  // Check if current page is login page
  const isLoginPage = location.pathname === '/login';

  // Determine current page type
  const getCurrentPage = () => {
    if (location.pathname === '/institution-dashboard') return 'institution';
    if (location.pathname === '/student-dashboard') return 'student';
    if (location.pathname === '/admin-dashboard') return 'admin';
    return 'home';
  };

  useEffect(() => {
    // Check if user is logged in
    const userId = localStorage.getItem('userId');
    const userType = localStorage.getItem('userType');
    
    if (userId && userType) {
      setUser({ id: userId, type: userType });
    }
  }, [location]);

  const handleLogout = () => {
    localStorage.removeItem('userId');
    localStorage.removeItem('userType');
    setUser(null);
    navigate('/');
  };

  const handleLoginSuccess = () => {
    // Refresh user state
    const userId = localStorage.getItem('userId');
    const userType = localStorage.getItem('userType');
    if (userId && userType) {
      setUser({ id: userId, type: userType });
    }
  };

  return (
    <>
      {!isLoginPage && (
        <Navigation 
          onLoginClick={() => navigate('/login')}
          isLoggedIn={!!user}
          onLogout={handleLogout}
          userType={user?.type}
          currentPage={getCurrentPage()}
        />
      )}
      
      <Routes>
        <Route path="/" element={<HomePage />} />
        <Route path="/login" element={<Login onLoginSuccess={handleLoginSuccess} />} />
        <Route path="/institution-dashboard" element={<AcademicInstitution />} />
        <Route path="/student-dashboard" element={<MyVerifiED />} />
        <Route path="/admin-dashboard" element={<AdminDashboard />} />
      </Routes>
    </>
  );
}

function App() {
  return (
    <BrowserRouter>
      <AppContent />
    </BrowserRouter>
  );
}

export default App;